import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {environment} from '../environments/environment'

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  //  logingUrl:String='http://localhost:8080/';
  logingUrl:String=environment.logingUrl;


  constructor(private http:HttpClient) { }
  login(obj){
   return( this.http.post(`${this.logingUrl}login`,obj))
   }
 
   getEmp(){
    return ( this.http.get(this.logingUrl+"getallemployee"))
  }

  grtEmpBYId(employeeid){
    return( this.http.get(`${this.logingUrl}getemployeebyid/${employeeid}`))
   }


GetCountry(){
return ( this.http.get(`${this.logingUrl}getallcountry`))
}

AddEmployee(obj){
return (this.http.post(`${this.logingUrl}saveemployee`,obj,{responseType:'text'}))// backend kadun post madhe string return  ahe 
                                              //tymule respone type text but backend kadun json obj yet asen tr no need to menstion becuse angular take it self json obj
}


updateEmployee(obj){
return (this.http.put(`${this.logingUrl}updateemployee`,obj,{responseType:'text'}))
}
}
